<template>
  <ContentBase>
    <el-container v-if="dataLoaded">
      <el-header>
        <el-button type="primary" @click="showAddDialog">AI</el-button>
      </el-header>
      <el-main>
        <el-table :data="prompts" empty-text="AI">
          <el-table-column label="" prop="name"/>
          <el-table-column label="" prop="description"/>
          <el-table-column label="">
            <template #default="scope">
              <el-tag v-if="scope.row.is_default" type="success"></el-tag>
              <el-button v-else type="text" @click="setDefaultPrompt(scope.row)"></el-button>
            </template>
          </el-table-column>
          <el-table-column label="" prop="created_at" :formatter="formatDateTime"/>
          <el-table-column label="">
            <template #default="scope">
              <el-button v-if="canEdit(scope.row)" @click="handleEditPrompt(scope.row)"></el-button>
              <el-button v-if="canEdit(scope.row)" @click="handleDeletePrompt(scope.row)" type="danger"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-main>
    </el-container>

    <!-- / -->
    <el-dialog v-model="formVisible" :title="formTitle" width="800px">
      <el-form :model="promptForm" :rules="formRules" ref="promptFormRef">
        <el-form-item label="" prop="name">
          <el-input v-model="promptForm.name" placeholder=""/>
        </el-form-item>
        <el-form-item label="">
          <el-input 
            v-model="promptForm.description" 
            placeholder=""
          />
        </el-form-item>
        <el-form-item label="" prop="content">
          <el-input 
            v-model="promptForm.content" 
            type="textarea"
            placeholder=""
            :rows="8"
            resize="vertical"
          />
        </el-form-item>
        <el-form-item>
          <el-checkbox v-model="promptForm.is_default"></el-checkbox>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="cancelForm"></el-button>
          <el-button type="primary" @click="confirmForm"></el-button>
        </span>
      </template>
    </el-dialog>
  </ContentBase>
</template>

<script setup>
import {computed, nextTick, onMounted, reactive, ref} from "vue";
import {addAIPrompt, deleteAIPrompt, queryAIPrompts, updateAIPrompt} from "@/assets/js/module/aiprompt";
import {
  ElButton,
  ElCheckbox,
  ElContainer,
  ElDialog,
  ElForm,
  ElFormItem,
  ElHeader,
  ElInput,
  ElMain,
  ElMessage,
  ElMessageBox,
  ElNotification,
  ElTable,
  ElTableColumn,
  ElTag,
} from "element-plus";
import "element-plus/dist/index.css";
import {useStore} from "vuex";
import ContentBase from "@/components/ContentBase.vue";
import {formatDateTime} from "@/assets/js/util/datetime_util";

const store = useStore();

// 
const checkResponse = (response) => {
  if (response == null || response.code === 2) {
    store.dispatch("logout")
    location.reload();
  }
}

// 
const dataLoaded = ref(false);
const prompts = reactive([]);
const formVisible = ref(false);
const isEditing = ref(false);
const promptFormRef = ref(null);

// 
const promptForm = reactive({
  id: null,
  name: '',
  description: '',
  content: '',
  is_default: false
});

// 
const formRules = {
  name: [{ required: true, message: '', trigger: 'blur' }],
  content: [{ required: true, message: '', trigger: 'blur' }]
};

// 
const formTitle = computed(() => isEditing.value ? 'AI' : 'AI');

// 
const canEdit = (prompt) => {
  return prompt.user_id !== 0; // 
}

// 
onMounted(async () => {
  await loadPrompts();
});

// 
const loadPrompts = async () => {
  dataLoaded.value = false;
  try {
    const response = await queryAIPrompts();
    checkResponse(response);
    if (response.code === 0) {
      prompts.splice(0, prompts.length, ...response.data);
    }
  } catch (error) {
    ElNotification({
      title: '',
      message: '',
      type: 'error',
    });
  } finally {
    dataLoaded.value = true;
  }
};

// 
const showAddDialog = () => {
  isEditing.value = false;
  resetForm();
  formVisible.value = true;
};

// 
const handleEditPrompt = (prompt) => {
  if (!canEdit(prompt)) {
    ElMessage.warning('');
    return;
  }
  isEditing.value = true;
  Object.assign(promptForm, prompt);
  formVisible.value = true;
};

// 
const handleDeletePrompt = async (prompt) => {
  if (!canEdit(prompt)) {
    ElMessage.warning('');
    return;
  }
  
  try {
    await ElMessageBox.confirm(
      ` "${prompt.name}" `,
      '',
      {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      }
    );
    
    const response = await deleteAIPrompt(prompt.id);
    checkResponse(response);
    
    if (response.code === 0) {
      ElNotification({
        title: '',
        message: '',
        type: 'success',
      });
      await loadPrompts();
    }
  } catch (error) {
    if (error !== 'cancel') {
      ElNotification({
        title: '',
        message: '',
        type: 'error',
      });
    }
  }
};

// 
const setDefaultPrompt = async (prompt) => {
  try {
    const response = await updateAIPrompt(prompt.id, { is_default: true });
    checkResponse(response);
    
    if (response.code === 0) {
      ElNotification({
        title: '',
        message: '',
        type: 'success',
      });
      await loadPrompts();
    }
  } catch (error) {
    ElNotification({
      title: '',
      message: '',
      type: 'error',
    });
  }
};

// 
const resetForm = () => {
  Object.assign(promptForm, {
    id: null,
    name: '',
    description: '',
    content: '',
    is_default: false
  });
  nextTick(() => {
    promptFormRef.value?.clearValidate();
  });
};

// 
const cancelForm = () => {
  formVisible.value = false;
  resetForm();
};

// 
const confirmForm = async () => {
  if (!promptFormRef.value) return;
  
  try {
    await promptFormRef.value.validate();
    
    ElNotification({
      title: '',
      message: `${isEditing.value ? '' : ''}...`,
      type: 'info',
    });
    
    let response;
    if (isEditing.value) {
      // 
      response = await updateAIPrompt(promptForm.id, promptForm);
    } else {
      // 
      response = await addAIPrompt(promptForm);
    }
    
    checkResponse(response);
    
    if (response.code === 0) {
      ElNotification({
        title: '',
        message: `${isEditing.value ? '' : ''}`,
        type: 'success',
      });
      formVisible.value = false;
      resetForm();
      await loadPrompts();
    }
  } catch (error) {
    if (error.errors) {
      ElMessage.warning('');
    } else {
      ElNotification({
        title: '',
        message: `${isEditing.value ? '' : ''}`,
        type: 'error',
      });
    }
  }
};
</script>

<style scoped>
.dialog-footer button:first-child {
  margin-right: 10px;
}
</style>
