import LoginView from "@/views/user/LoginView.vue";
import RegisterView from "@/views/user/RegisterView.vue";

const AuthRoutes = [
    {
        path: '/login',
        name: 'login',
        component: LoginView,
        beforeEnter: (to, from, next) => {
            // 
            next(vm => {
                vm.$nextTick(() => {
                    //  ref  'loginMenuItem'
                    vm.$refs.loginMenuItem.focus();
                });
            });
        },
    },
    {
        path: '/register',
        name: 'register',
        component: RegisterView
    },
]

export default AuthRoutes;