<template>
  <div id="app">
    <div class="container">
      <h1></h1>
      <div class="controls">
        <button class="btn" @click="startRecording"></button>
        <button class="btn" @click="stopRecording"></button>
        <input class="input" type="number" v-model="duration" placeholder=""/>
      </div>
      <input class="input" v-if="countdown > 0" v-model="countdown" placeholder="" readonly/>
      <audio v-if="audioUrl" :src="audioUrl" controls></audio>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      mediaRecorder: null,
      audioChunks: [],
      audioUrl: null,
      countdown: 0, // 
      countdownInterval: null, // Interval
      duration: 45, // 45
    };
  },
  methods: {
    async startRecording() {
      try {
        const stream = await navigator.mediaDevices.getUserMedia({audio: true});
        this.mediaRecorder = new MediaRecorder(stream);
        this.audioChunks = [];

        this.mediaRecorder.ondataavailable = event => {
          this.audioChunks.push(event.data);
        };

        this.mediaRecorder.start();
        this.startCountdown(this.duration); // 
      } catch (err) {
        console.error('Error accessing audio', err);
      }
    },
    stopRecording() {
      this.mediaRecorder.stop();
      this.mediaRecorder.onstop = () => {
        const audioBlob = new Blob(this.audioChunks);
        this.audioUrl = URL.createObjectURL(audioBlob);
      };
      this.stopCountdown();
    },
    startCountdown(duration) {
      this.countdown = duration;
      this.countdownInterval = setInterval(() => {
        this.countdown--;
        if (this.countdown <= 0) {
          this.stopRecording();
        }
      }, 1000);
    },
    stopCountdown() {
      clearInterval(this.countdownInterval);
      this.countdown = 0; // 
    },
  },
};
</script>

