<template>
  <ContentBase @keydown="handleKeydown" tabindex="0">
    <!-- Katex CSS for math formula rendering -->
    <link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/katex@0.16.9/dist/katex.min.css" crossorigin="anonymous">
    <div v-if="dataStatus === 1" class="word-card">
      <div class="header">
        <el-tooltip content="LLM" placement="top">
          <el-button v-if="showHeader" class="centered-button" circle @click="toggleLLMPanel">
            <el-icon>
              <ChatDotRound/>
            </el-icon>
          </el-button>
        </el-tooltip>
        &nbsp;
        <el-tooltip content=" (: S)" placement="top">
          <el-button v-if="showHeader" class="centered-button" circle @click="showSearchDrawer = !showSearchDrawer">
            <el-icon>
              <Search/>
            </el-icon>
          </el-button>
        </el-tooltip>
        &nbsp;
        <el-tooltip content=" (Markdown)" placement="top">
          <el-button v-if="showHeader" class="centered-button" circle @click="showEditNoteDrawerButtonClicked">
            <el-icon>
              <EditPen/>
            </el-icon>
          </el-button>
        </el-tooltip>
        &nbsp;
        <el-tooltip content=" (: D)" placement="top">
          <el-button v-if="showHeader" class="centered-button" circle @click="markAsUnwanted">
            <el-icon>
              <Delete/>
            </el-icon>
          </el-button>
        </el-tooltip>
      </div>

      <!--  -->
      <div class="word-display">
        <div class="word-title-section">
          <h1 class="word">{{ words[idx].word }}</h1>
        </div>

        <!--  -->
        <div v-if="status === 2 || status === 3" class="meaning-container">
          <div v-if="isFetchingDefinition" class="fetching-meaning">
            ...
          </div>
          <div v-else-if="hasMeaning" class="meaning-content" v-html="currentWordMeaning"
               @click="handleWordContentClick"></div>
          <div v-else class="no-meaning">
            
          </div>
        </div>
      </div>

      <div class="footer">
        <el-button v-if="status === 1" @click="handleFamiliar">(J)</el-button>
        <el-button v-if="status === 1" @click="handleUnfamiliar">(K)</el-button>
        <el-button v-if="status === 2" @click="gotItWrong">(U)</el-button>
        <el-button v-if="status === 2 || status === 3" @click="showNextWord">(N)</el-button>
      </div>
    </div>
    <div v-else-if="dataStatus === 2" class="word-card">
      <h1 class="word">Loading data...</h1>
    </div>
    <div v-else-if="dataStatus === 3" class="word-card">
      <h1 class="word"></h1>
    </div>

    <!-- Search Drawer ContentBaseContentBasesContentBase -->
    <el-drawer v-model="showSearchDrawer" title="" :with-header="true">
      <p v-for="(dictionary, index) in dictionaries" :key="dictionary.id">
        <a :href="dictionary.prefix + words[idx].word + (dictionary.suffix || '') " target="_blank">
          <b>{{ index + 1 }}. {{ dictionary.title }}</b>
        </a>
        <el-divider/>
      </p>
    </el-drawer>
  </ContentBase>

  <el-drawer v-model="showEditNoteDrawer" title=" (Markdown)" :with-header="true" size="50%">
    <div class="note-editor">
      <el-tabs v-model="activeTab" type="border-card">
        <el-tab-pane label="" name="edit">
          <el-input
              type="textarea"
              placeholder=" (Markdown)"
              v-model="currentWordNote"
              :rows="20"
              maxlength="1000"
              show-word-limit
          ></el-input>
        </el-tab-pane>
        <el-tab-pane label="" name="preview">
          <div class="markdown-preview" v-html="renderedMarkdown"></div>
        </el-tab-pane>
      </el-tabs>
      <el-divider/>
      <div class="editor-actions">
        <el-button type="primary" @click="updateNote"></el-button>
        <el-button @click="showEditNoteDrawer = false"></el-button>
      </div>
    </div>
  </el-drawer>

  <!-- LLM -->
  <LLMQueryPanel
      v-if="showLLMPanel"
      :current-word="getCurrentWord()"
      @close="showLLMPanel = false"
  />
</template>

<script setup>
import {ElButton, ElDivider, ElDrawer, ElIcon, ElInput, ElTabPane, ElTabs, ElTooltip} from 'element-plus';
import {ChatDotRound, Delete, EditPen, Search} from "@element-plus/icons-vue";
import ContentBase from "@/components/ContentBase.vue";
import LLMQueryPanel from "@/components/LLMQueryPanel.vue";
import MarkdownIt from 'markdown-it';

import {useStore} from "vuex";
import {computed, defineProps, onMounted, reactive, ref} from "vue";

import {getUserProfile} from "@/assets/js/module/user/query";
import {getDictionaryList} from "@/assets/js/module/dictionary/query";
import {fetchWords} from "@/assets/js/module/entry/query";
import {formatDefinition, getWordDefinition} from "@/assets/js/util/dictionary_api";
import {
  resetEntryStudyCountToZero,
  setUnwanted,
  updateEntry,
  updateEntryStudyCount
} from "@/assets/js/module/entry/update";
// markdown
import MarkdownItMark from 'markdown-it-mark';
import MarkdownItFootnote from 'markdown-it-footnote';
import MarkdownItAbbr from 'markdown-it-abbr';
import MarkdownItIns from 'markdown-it-ins';
import MarkdownItSub from 'markdown-it-sub';
import MarkdownItSup from 'markdown-it-sup';
import MarkdownItTexmath from 'markdown-it-texmath';
import katex from 'katex';

const props = defineProps(["type"]);
/*
 * 1: 
 * 2: 
 * 3: 
 */
const dataStatus = ref(2);

const showHeader = ref(true);
const showSearchDrawer = ref(false);
const showEditNoteDrawer = ref(false);
const showLLMPanel = ref(false);
const activeTab = ref('edit');
const isFetchingDefinition = ref(false);

const md = new MarkdownIt({
  html: true,
  linkify: true,
  typographer: true,
  tables: true
})
    .use(MarkdownItMark)
    .use(MarkdownItFootnote)
    .use(MarkdownItAbbr)
    .use(MarkdownItIns)
    .use(MarkdownItSub)
    .use(MarkdownItSup)
    .use(MarkdownItTexmath, {
      engine: katex,
      delimiters: 'dollars',
      katexOptions: {
        macros: {
          "\\RR": "\\mathbb{R}"
        }
      }
    });

const showEditNoteDrawerButtonClicked = () => {
  setCurrentWordNote();
  activeTab.value = 'edit';
  showEditNoteDrawer.value = !showEditNoteDrawer.value;
}

// markdown
const renderedMarkdown = computed(() => {
  if (!currentWordNote.value) {
    return '<p class="empty-note"></p>';
  }
  return md.render(currentWordNote.value);
});

let dictionaries = reactive([]);

let words = reactive([]);
let idx = ref(0);
let countRecognitionTime = {};
const timesCountedAsKnown = ref(0);

// 
const getCurrentWord = () => {
  if (words.length > 0 && idx.value < words.length) {
    return words[idx.value].word || '';
  }
  return '';
}

const toggleLLMPanel = () => {
  showLLMPanel.value = !showLLMPanel.value;
}

/*
 * 1: 
 * 2: 
 * 3: 
 */
const status = ref(1);

const checkResponse = (response) => {
  if (response == null || response.code === 2) {
    const store = useStore();
    store.dispatch("logout")
    location.reload();
  }
}

onMounted(
    async () => {
      const fetchWordsResponse = await fetchWords(props.type);
      checkResponse(fetchWordsResponse)
      words = fetchWordsResponse.data;
      if (words == null || words.length === 0) {
        dataStatus.value = 3;
        return;
      } else {
        dataStatus.value = 1;
        for (let i = 0; i < words.length; i++) {
          countRecognitionTime[i] = 0;
        }
      }

      const getDictionaryListResponse = await getDictionaryList();
      checkResponse(getDictionaryListResponse);
      dictionaries = getDictionaryListResponse.data;

      const getUserProfileResponse = await getUserProfile();
      checkResponse(getUserProfileResponse);
      timesCountedAsKnown.value = getUserProfileResponse.times_counted_as_known;

      // 
      if (dataStatus.value === 1 && words.length > 0) {
        await checkAndFetchDefinition();
      }
    }
);

const handleKeydown = async (event) => {
  if (!showEditNoteDrawer.value) {
    if ('0123456789'.includes(event.key)) {
      let number = +event.key;
      if (number === 0) number = 10;
      if (number <= dictionaries.length) {
        window.open(dictionaries[number - 1].prefix + words[idx.value].word + (dictionaries[number - 1].suffix || ""), '_blank');
      }
    } else if (event.key === 's') {
      showSearchDrawer.value = !showSearchDrawer.value;
    } else if (event.key === "n") {
      if (status.value !== 2 && status.value !== 3) {
        return true;
      }
      await showNextWord();
    } else if (event.key === "u") {
      if (status.value !== 2) {
        return true;
      }
      await gotItWrong();
    } else if (event.key === "j") {
      if (status.value !== 1) {
        return true;
      }
      await handleFamiliar();
    } else if (event.key === "k") {
      if (status.value !== 1) {
        return true;
      }
      await handleUnfamiliar();
    } else if (event.key === "d" || event.key === "D") {
      await markAsUnwanted();
    } else {
      return true;
    }
  }
};

const markAsUnwanted = async () => {
  countRecognitionTime[idx.value] = timesCountedAsKnown.value;
  const markedAsUnwantedResponse = await setUnwanted(words[idx.value].id);
  checkResponse(markedAsUnwantedResponse);
  await showNextWord();
};

const handleUnfamiliar = async () => {
  countRecognitionTime[idx.value] = 0;
  const resetEntryStudyCountToZeroResponse = await resetEntryStudyCountToZero(words[idx.value].id);
  checkResponse(resetEntryStudyCountToZeroResponse);
  status.value = 3;
};

const handleFamiliar = async () => {
  countRecognitionTime[idx.value]++;
  if (countRecognitionTime[idx.value] >= timesCountedAsKnown.value) {
    const updateEntryStudyCountResponse = await updateEntryStudyCount(words[idx.value].id);
    checkResponse(updateEntryStudyCountResponse);
  }
  status.value = 2;
};

const gotItWrong = async () => {
  countRecognitionTime[idx.value] = 0;
  const resetEntryStudyCountToZeroResponse = await resetEntryStudyCountToZero(words[idx.value].id);
  checkResponse(resetEntryStudyCountToZeroResponse);
  await showNextWord();
}

let currentWordNote = ref('');
let currentWordMeaning = ref('');
const setCurrentWordNote = () => {
  currentWordNote.value = words[idx.value].note;
};

// 
const hasMeaning = computed(() => {
  // API
  const wordMeaning = words[idx.value]?.meaning || currentWordMeaning.value;
  return wordMeaning && wordMeaning.trim() !== '';
});

const checkAndFetchDefinition = async () => {
  const currentWord = words[idx.value];
  if (!currentWord) return;

  const meaning = currentWord.meaning || '';
  // API
  if (!meaning || meaning.trim() === '' && !isFetchingDefinition.value) {
    isFetchingDefinition.value = true;
    currentWordMeaning.value = '';

    try {
      const result = await getWordDefinition(currentWord.word);

      if (result.success) {
        currentWordMeaning.value = formatDefinition(result);
        console.log(` "${currentWord.word}" `);
      } else {
        currentWordMeaning.value = '';
        console.log(` "${currentWord.word}" :`, result);
      }
    } catch (error) {
      console.error(` "${currentWord.word}" :`, error);
      currentWordMeaning.value = '';
    } finally {
      isFetchingDefinition.value = false;
    }
  } else {
    currentWordMeaning.value = meaning;
    console.log(` "${currentWord.word}" :`, meaning);
  }
};

// 
const showNextWord = async () => {
  let tempIdx = idx.value;
  do {
    tempIdx = (tempIdx + 1) % words.length;
  } while (tempIdx !== idx.value && countRecognitionTime[tempIdx] >= timesCountedAsKnown.value);
  if (tempIdx === idx.value && countRecognitionTime[tempIdx] >= timesCountedAsKnown.value) {
    dataStatus.value = 3;
  } else {
    status.value = 1;
    idx.value = tempIdx;

    // 
    await checkAndFetchDefinition();
  }
}

const updateNote = async () => {
  const updateNoteResponse = await updateEntry(words[idx.value].id, words[idx.value].word, words[idx.value].meaning, words[idx.value].book_id, currentWordNote.value, words[idx.value].unwanted, words[idx.value].study_count, words[idx.value].date_to_review, words[idx.value].created_at);
  checkResponse(updateNoteResponse);
}

//  - 
const handleWordContentClick = (event) => {
  // 
  if (event.target.classList.contains('audio-btn')) {
    const audioUrl = event.target.getAttribute('data-audio');
    if (audioUrl) {
      playAudio(audioUrl);
    }
  }
};

// 
const playAudio = (audioUrl) => {
  try {
    const audio = new Audio(audioUrl);
    audio.play().catch(error => {
      console.error(':', error);
    });
  } catch (error) {
    console.error(':', error);
  }
};
</script>

