<template>
  <ContentBase>
    <el-container v-if="dataLoaded">
      <el-header>
        <el-button type="primary" @click="showAddDialog">LLM</el-button>
      </el-header>
      <el-main>
        <el-table :data="services" empty-text="LLM">
          <el-table-column label="" prop="name"/>
          <el-table-column label="" prop="provider"/>
          <el-table-column label="API" prop="endpoint"/>
          <el-table-column label="" prop="model"/>
          <el-table-column label="">
            <template #default="scope">
              <el-tag v-if="scope.row.is_default" type="success"></el-tag>
              <el-button v-else type="text" @click="setDefaultService(scope.row)"></el-button>
            </template>
          </el-table-column>
          <el-table-column label="" prop="description"/>
          <el-table-column label="" prop="created_at" :formatter="formatDateTime"/>
          <el-table-column label="">
            <template #default="scope">
              <el-button @click="handleEditService(scope.row)"></el-button>
              <el-button @click="handleDeleteService(scope.row)" type="danger"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-main>
    </el-container>

    <!-- / -->
    <el-dialog v-model="formVisible" :title="formTitle">
      <el-form :model="serviceForm" :rules="formRules" ref="serviceFormRef">
        <el-form-item label="" prop="name">
          <el-input v-model="serviceForm.name" placeholder=""/>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="serviceForm.provider" placeholder=""/>
        </el-form-item>
        <el-form-item label="API" prop="endpoint">
          <el-input v-model="serviceForm.endpoint" placeholder="APIURL"/>
        </el-form-item>
        <el-form-item label="" prop="model">
          <el-input v-model="serviceForm.model" placeholder=""/>
        </el-form-item>
        <el-form-item label="API Key" prop="api_key">
          <el-input
              v-model="serviceForm.api_key"
              type="password"
              placeholder="API Key"
              show-password
          />
        </el-form-item>
        <el-form-item label="">
          <el-input
              v-model="serviceForm.description"
              type="textarea"
              placeholder=""
              :rows="3"
          />
        </el-form-item>
        <el-form-item>
          <el-checkbox v-model="serviceForm.is_default"></el-checkbox>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="cancelForm"></el-button>
          <el-button type="primary" @click="confirmForm"></el-button>
        </span>
      </template>
    </el-dialog>
  </ContentBase>
</template>

<script setup>
import {computed, nextTick, onMounted, reactive, ref} from "vue";
import {addLLMService, deleteLLMService, queryLLMServices, updateLLMService} from "@/assets/js/module/llm";
import {
  ElButton,
  ElCheckbox,
  ElContainer,
  ElDialog,
  ElForm,
  ElFormItem,
  ElHeader,
  ElInput,
  ElMain,
  ElMessage,
  ElMessageBox,
  ElNotification,
  ElTable,
  ElTableColumn,
  ElTag,
} from "element-plus";
import "element-plus/dist/index.css";
import {useStore} from "vuex";
import ContentBase from "@/components/ContentBase.vue";
import {formatDateTime} from "@/assets/js/util/datetime_util";

const store = useStore();

// 
const checkResponse = (response) => {
  if (response == null || response.code === 2) {
    store.dispatch("logout")
    location.reload();
  }
}

// 
const dataLoaded = ref(false);
const services = reactive([]);
const formVisible = ref(false);
const isEditing = ref(false);
const serviceFormRef = ref(null);

// 
const serviceForm = reactive({
  id: null,
  name: '',
  provider: '',
  endpoint: '',
  model: '',
  api_key: '',
  description: '',
  is_default: false
});

// 
const formRules = {
  name: [{required: true, message: '', trigger: 'blur'}],
  endpoint: [{required: true, message: 'API', trigger: 'blur'}],
  model: [{required: true, message: '', trigger: 'blur'}],
  api_key: [{required: true, message: 'API Key', trigger: 'blur'}]
};

// 
const formTitle = computed(() => isEditing.value ? 'LLM' : 'LLM');

// 
onMounted(async () => {
  await loadServices();
});

// 
const loadServices = async () => {
  dataLoaded.value = false;
  try {
    const response = await queryLLMServices();
    checkResponse(response);
    if (response.code === 0) {
      services.splice(0, services.length, ...response.data);
    }
  } catch (error) {
    ElNotification({
      title: '',
      message: '',
      type: 'error',
    });
  } finally {
    dataLoaded.value = true;
  }
};

// 
const showAddDialog = () => {
  isEditing.value = false;
  resetForm();
  formVisible.value = true;
};

// 
const handleEditService = (service) => {
  isEditing.value = true;
  Object.assign(serviceForm, service);
  formVisible.value = true;
};

// 
const handleDeleteService = async (service) => {
  try {
    await ElMessageBox.confirm(
        ` "${service.name}" `,
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
        }
    );

    const response = await deleteLLMService(service.id);
    checkResponse(response);

    if (response.code === 0) {
      ElNotification({
        title: '',
        message: '',
        type: 'success',
      });
      await loadServices();
    }
  } catch (error) {
    if (error !== 'cancel') {
      ElNotification({
        title: '',
        message: '',
        type: 'error',
      });
    }
  }
};

// 
const setDefaultService = async (service) => {
  try {
    const response = await updateLLMService(service.id, {is_default: true});
    checkResponse(response);

    if (response.code === 0) {
      ElNotification({
        title: '',
        message: '',
        type: 'success',
      });
      await loadServices();
    }
  } catch (error) {
    ElNotification({
      title: '',
      message: '',
      type: 'error',
    });
  }
};

// 
const resetForm = () => {
  Object.assign(serviceForm, {
    id: null,
    name: '',
    provider: '',
    endpoint: '',
    model: '',
    api_key: '',
    description: '',
    is_default: false
  });
  nextTick(() => {
    serviceFormRef.value?.clearValidate();
  });
};

// 
const cancelForm = () => {
  formVisible.value = false;
  resetForm();
};

// 
const confirmForm = async () => {
  if (!serviceFormRef.value) return;

  try {
    await serviceFormRef.value.validate();

    ElNotification({
      title: '',
      message: `${isEditing.value ? '' : ''}...`,
      type: 'info',
    });

    let response;
    if (isEditing.value) {
      // 
      response = await updateLLMService(serviceForm.id, serviceForm);
    } else {
      // 
      response = await addLLMService(serviceForm);
    }

    checkResponse(response);

    if (response.code === 0) {
      ElNotification({
        title: '',
        message: `${isEditing.value ? '' : ''}`,
        type: 'success',
      });
      formVisible.value = false;
      resetForm();
      await loadServices();
    }
  } catch (error) {
    if (error.errors) {
      ElMessage.warning('');
    } else {
      ElNotification({
        title: '',
        message: `${isEditing.value ? '' : ''}`,
        type: 'error',
      });
    }
  }
};
</script>

<style scoped>
.dialog-footer button:first-child {
  margin-right: 10px;
}
</style>