<template>
  <ContentBase>
    <div class="register-container">
      <el-form ref="registerForm" label-width="80px">
        <el-form-item label="" prop="serverLink" class="form-item">
          <el-input v-model="serverLink" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="username" class="form-item">
          <el-input v-model="username" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="email" class="form-item">
          <el-input v-model="email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="avatar" class="form-item">
          <el-input v-model="avatar" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" class="form-item">
          <el-input type="password" v-model="password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword" class="form-item">
          <el-input type="password" v-model="confirmPassword" @keyup.enter="handleRegister"
                    placeholder=""></el-input>
        </el-form-item>

        <div v-if="registerError" style="color: red; margin-bottom: 10px; margin-left: 100px;">
          {{ errorMessage }}
        </div>

        <el-form-item class="form-item">
          <el-button type="primary" @click="handleRegister" class="register-button"></el-button>
        </el-form-item>

        <p v-if="!registerSuccess" @click="login" style="margin-left: 80px; color: #04eefa"></p>
        <p v-if="registerSuccess" style="margin-left: 80px; color: #0477fa">{{ registerSuccessMessage }}</p>
      </el-form>
    </div>
  </ContentBase>
</template>

<script setup>
import "element-plus/dist/index.css"
import {ref} from 'vue';
import {ElButton, ElForm, ElFormItem, ElInput} from "element-plus";
import {useRouter} from "vue-router";
import {useStore} from "vuex";
import ContentBase from "@/components/ContentBase.vue";

const store = useStore();

const serverLink = ref('http://127.0.0.1:8080');
const username = ref('');
const email = ref('');
const avatar = ref('');
const password = ref('');
const confirmPassword = ref('');
const registerError = ref(false);
const errorMessage = ref('');
const registerSuccess = ref(false);
const registerSuccessMessage = ref('');

const handleRegister = async () => {
  if (password.value !== confirmPassword.value) {
    registerError.value = true;
    errorMessage.value = '';
    return;
  }
  const response = await store.dispatch('register', {
    'serverLink': serverLink.value,
    'username': username.value,
    'email': email.value,
    'avatar': avatar.value,
    'password': password.value,
    'confirm_password': confirmPassword.value,
  });
  if (response === true) {
    registerError.value = false;
    registerSuccess.value = true;
    registerSuccessMessage.value = ', ';
    setTimeout(() => {
      login();
    }, 2000);
  } else {
    registerError.value = true;
    errorMessage.value = response;
  }
};

const router = useRouter();
const login = () => {
  router.push({name: 'login'});
};
</script>

<style scoped>
.register-container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
}

.form-item {
  width: 100%;
}

.register-button {
  width: 100%;
}
</style>
